
.wrapper[data-v-2]{position:absolute;padding:8px;width:504px;height:504px;background:#BBADA0;font-size:0;border-radius:10px
}
.wrapper .back[data-v-2]{display:inline-block;margin:8px;width:110px;height:110px;border-radius:6px;background:#CCC0B3
}
.game[data-v-2]{position:absolute;padding:8px;width:504px;height:504px
}
.game .line[data-v-2]{margin-bottom:16px;height:110px
}
.game .ceil[data-v-2]{display:inline-block;vertical-align:top;margin:8px;width:110px;height:110px;line-height:110px;border-radius:6px;background:#EDE0C8;text-align:center;color:#776e65;font-size:60px;visibility:hidden
}
.game .trans[data-v-2]{transition:all .3s ease
}
.game .visib[data-v-2]{visibility:visible
}
.game .color[data-v-2]{color:#fff
}
.game .font[data-v-2]{font-size:40px
}


